2: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
<template>
	<div style="width: 600px; height: 600px; border: 1px solid red; margin: auto; position: relative">
		<div v-for="(row, rowIndex) in randomizedGrid" style="display: flex; height: 200px; width: 100%">
			<div
				v-for="(piece, index) in row"
				:ref="(el) => pieceRefs.push({ element: el, gridPos: { rowIndex, index } })"
				style="flex: 1; border: 1px solid red; opacity: 0"
			>
				{{ piece }}
			</div>
		</div>
		<div
			v-for="(piece, pieceIndex) in puzzlePieces"
			@click="handleSelectPiece(piece, pieceIndex)"
			:style="`transition:all 0.3s ease;${piece.value ? 'border:1px solid red' : ''};${
				selectedPiece.value === piece.value ? 'background:#ffffff14;' : ''
			}display:flex;transform:translate(${piece.offsetLeft}px, ${
				piece.offsetTop
			}px);position:absolute;top:0px;left:0px;width:200px;height:200px;`"
		>
			<span style="margin: auto; color: #fff">{{ piece.value }}</span>
		</div>
		{{ trackerGrid }}
		{{ selectedPiece }}
		{{ itterations }}
	</div>
</template>

<script>
import { ref, onMounted, nextTick, onUpdated, watch, computed } from 'vue';
export default {
	name: 'App',
	components: {},
	setup() {
		const pieceRefs = ref([]);

		let grid = [
			[null, '2', '3'],
			['4', '5', '6'],
			['7', '8', '9'],
		];

		const selectedPiece = ref({});
		const puzzlePieces = ref([]);
		const trackerGrid = ref([]);
		const itterations = ref(0);

		const randomizedGrid = computed(() => {
			const flattened = grid
				.reduce((acc, curr) => {
					for (const n of curr) {
						acc.push(n);
					}
					return acc;
				}, [])
				.sort(() => Math.random() - 0.5);

			let count = 0;
			let randomizedGrid = [];
			let row = [];
			for (const n of flattened) {
				row.push(n);
				count++;
				if (count === 3) {
					count = 0;
					randomizedGrid.push(row);
					row = [];
				}
			}

			console.log(randomizedGrid);
			grid = randomizedGrid;
			trackerGrid.value = flattened;
			return randomizedGrid;
		});

		const test = () => {
			let possibilities = [];

			for (const [ri, row] of grid.entries()) {
				for (const [i, piece] of row.entries()) {
					if (!piece) {
						if (i - 1 > -1) {
							possibilities.push(grid[ri][i - 1]);
						}
						if (ri - 1 > -1) {
							possibilities.push(grid[ri - 1][i]);
						}
						if (i + 1 < grid.length) {
							possibilities.push(grid[ri][i + 1]);
						}
						if (ri + 1 < grid.length) {
							possibilities.push(grid[ri + 1][i]);
						}
					}
				}
			}
			return possibilities;
		};

		let selectPieceTimeout = '';
		let adaTimeout = '';

		let correct = [];
		const ada = () => {
			const psblts = test();
			console.log(psblts);
			const rndmv = psblts[Math.floor(Math.random() * psblts.length)];
			const piece = puzzlePieces.value.find((piece) => piece.value === rndmv);
			if (piece.value === selectedPiece.value.value) {
				clearTimeout(selectPieceTimeout);
				clearTimeout(adaTimeout);
				ada();
				return;
			}

			const pieceIndex = puzzlePieces.value.findIndex((piece) => piece.value === rndmv);

			const nullPiece = puzzlePieces.value.find((piece) => piece.value === '');
			const nullIndex = puzzlePieces.value.findIndex((piece) => piece.value === '');
			handleSelectPiece(piece, pieceIndex);
			selectPieceTimeout = setTimeout(() => {
				handleSelectPiece(nullPiece, nullIndex);
			}, 300);

			adaTimeout = setTimeout(() => {
				ada();
			}, 200);
			itterations.value++;
		};

		const unsub = watch(
			pieceRefs,
			(elements) => {
				if (elements) {
					getPiecePositions();
					ada();
					unsub();
				}
			},
			{ deep: true }
		);

		const invalidMove = (xValue, yValue) => {
			return xValue < -1 || xValue > 1 || yValue < -1 || yValue > 1 || yValue === xValue || yValue + xValue === 0;
		};

		const handleSelectPiece = (piece, index) => {
			if (piece.value === '') {
				const { offsetLeft: moveOffsetLeft, offsetTop: moveOffsetTop, gridPos: moveGridPos } = piece;
				const {
					offsetLeft: selectedPieceOffsetLeft,
					offsetTop: selectedPieceOffsetTop,
					gridPos: selectedPieceGridPos,
				} = selectedPiece.value;

				const { rowIndex: moveRowIndex, index: movePieceIndex } = moveGridPos;
				const { rowIndex: selectedRowIndex, index: selectedPieceIndex } = selectedPieceGridPos;

				const xValue = selectedPieceIndex - movePieceIndex;
				const yValue = selectedRowIndex - moveRowIndex;

				if (invalidMove(xValue, yValue)) return;

				puzzlePieces.value[index].offsetLeft = selectedPieceOffsetLeft;
				puzzlePieces.value[index].offsetTop = selectedPieceOffsetTop;
				puzzlePieces.value[index].gridPos = selectedPieceGridPos;

				puzzlePieces.value[selectedPiece.value.index].offsetLeft = moveOffsetLeft;
				puzzlePieces.value[selectedPiece.value.index].offsetTop = moveOffsetTop;
				puzzlePieces.value[selectedPiece.value.index].gridPos = moveGridPos;

				const trackerNullIndex = trackerGrid.value.findIndex((n) => n === null);
				const trackerValueIndex = trackerGrid.value.findIndex((n) => n === selectedPiece.value.value);

				trackerGrid.value[trackerNullIndex] = selectedPiece.value.value;
				trackerGrid.value[trackerValueIndex] = null;

				selectedPiece.value = {
					value: selectedPiece.value.value,
					index: selectedPiece.value.index,
					offsetLeft: moveOffsetLeft,
					offsetTop: moveOffsetTop,
					gridPos: { rowIndex: moveRowIndex, index: movePieceIndex },
				};

				grid[moveRowIndex][movePieceIndex] = selectedPiece.value.value;
				grid[selectedRowIndex][selectedPieceIndex] = null;
				return;
			}
			selectedPiece.value = { ...piece, index };

			let correctCount = 0;

			for (const [index, n] of trackerGrid.value.entries()) {
				if (index === 0) {
					continue;
				}
				if (index + 1 == +n) {
					correctCount++;
				}
				if (correctCount + 1 === trackerGrid.value.length) {
					alert('YOU DID IT IN ' + itterations.value);
				}
			}
		};

		const getPiecePositions = () => {
			for (const el of pieceRefs.value) {
				const { offsetTop, offsetLeft, innerText } = el.element;
				puzzlePieces.value.push({ offsetTop, offsetLeft, gridPos: el.gridPos, value: innerText });
			}

			console.log(puzzlePieces);
		};

		return { grid, pieceRefs, randomizedGrid, puzzlePieces, selectedPiece, handleSelectPiece, trackerGrid, itterations };
	},
};
</script>

